.title-small {
  .text-options(24px; @black; 24px; none; 400;);
}

.title-small--stroked,
.title-small--stroked-doubled {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    border-top: 3px solid @green;
    z-index: -1;
    bottom: 0;
    right: calc(50% - 40px);
    left: calc(50% - 40px);
    margin-right: -50vw;
    margin-left: -50vw;
  }
}

.title-small--stroked {
  &::before {
    top: 70%;
  }
}

.title-small--stroked-doubled {
  &::before {
    top: 30%;
  }
}

@media (min-width: @tablet-width) {
  .title-small {
    position: relative;
    .text-options(36px; @black; 36px; none; 400;);
  }

  .title-small--stroked,
  .title-small--stroked-doubled {
    &::before {
      top: 70%;
    }
  }
}
